/* reset */

html, body, ol, ul, li, img, div, span, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ol, ul {
	list-style: none;
}




/* 전체 컨테이너 */




/* 상단 헤더 */

.main_top_nav {
  position: fixed;
  
  top: 0; right: 0; left: 0;
  background-color: #fff;
  padding: 0.7rem 2.4rem;
  z-index: 999;
  border-bottom: solid 1px #eaecef;
}

.main_top_nav .site_title {
  font-size: 1.3rem;
}

@media (max-width: 719px) {
  .site_title {
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}




/* 사이드 메뉴 */

.main_left_nav {
  position: fixed;
  width: 18rem;
  top: 3.2rem; bottom: 0; left: 0; 
  
  background-color: #fff;
  border-right: solid 1px black;

  overflow-y:scroll;
}

.main_left_nav .main_left_list {
  padding: 1.7rem 0;
}





/* 본문 */

.page {
  position: absolute;
  top: 3.2rem; left: 18rem; bottom: 0; right: 0;
  padding: 1.7rem 2.3rem;
  
}
