.main_page_container {
  position: relative;
  max-width: 740px;
  margin: 0 auto;
  
}

.mil_year {
  text-align: center;
  font-size: 5rem;
  margin-top: -1.5rem;
  margin-bottom: 3rem;
}

.mil_wrap {
  margin-top : 6rem
}

.mil_date {
  font-size: 1.8rem;
}

.mil_title {
  margin-top: 2.5rem;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

.mil_text_wrap {
  margin: 1.5rem 0;
}

.mil_text_title {
  font-size: 1rem;
  margin-bottom: 1rem;
}
.mil_text_title_text {
  font-size: 1.1rem;
  margin: 1.5rem 0;
  font-weight: bold;
}

.mil_text_content {
  margin-bottom: 1rem;
  padding-left: 0.5rem;
}


.mil_text_content li {
  margin-bottom: 0.5rem;
  line-height: 1.3rem;
}

.mil_text_content li span {
  display: block;
  margin: 0.7rem 0;

}

.mil_text_content li p {
  margin-bottom: 0.5rem;
  padding-left: 1rem;
}

.mil_text_content li p:before {
  content: '';
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  background-color: #000000;

  margin-bottom: 0.2rem;
  margin-right: 0.3rem;
}